<template>
  <div class="container-fluid">
    <nav class="navbar head" role="navigation">
        <div class="navbar-header">
            <a class="navbar-brand" href="#"><img src="../../assets/images/logo.png"></a>
        </div>
    </nav>
    <div class="row content">
        <div id="dialog">
            <form>
            <h3>洪泰孵化器后台管理系统</h3>
                <p><label for="">用户名：</label><input type="text" placeholder="用户名" v-model="form.username" id="userName"></p>
                <p><label for="">密码：</label><input type="password" placeholder="密码" v-model="form.password" id="psw"></p>
                <p><label for="">验证码：</label><input type="text" placeholder="验证码" v-model="form.captcha" id="vcode"></p>
                <p><span id="getImg"><img :src="imgUrl" style="width:30%; height:100%;"><label for="" id="changeOne"  @click="captcha">换一张</label></span></p>
                <p><input type="button" class="btn btn-success" id="dls" value="登录" @keyup.enter="signin" @click="signin"></p>
            </form>
        </div>
    </div>
</div>
</template>

<script>

export default {
    data () {
      return {
        imgUrl: '',
        form: {
          captcha: '',
          username: '',
          password: ''
        }
      }
    },
    created () {
        var _this = this
        $.ajax({
          url: '/main/api/v1/captcha',
          type: 'get',
          success: function (result) {
            _this.imgUrl = result
          }
        })
    },
    methods: {
        captcha() {
          var _this = this
          $.ajax({
            url: '/main/api/v1/captcha',
            type: 'get',
            success: function (result) {
              _this.imgUrl = result
            }
          })
        },
        signin() {
            var _this = this
            $.ajax({
              url: '/admin/api/v1/',
              type: 'post',
              contentType:'application/json',
              data: JSON.stringify(this.form),
              success: function (result) {
                sessionStorage.setItem('user', JSON.stringify(result))
                _this.$router.push('/admin_list')
              }
            })
        }
    }
}
</script>

<style lang="scss">
@import '../../assets/css/admin_index.css';
.header:nth-of-type(1) {
    padding-left: 0;
}
.header {
    padding-top: 0;

}
</style>



